.burgerMenuIcon {
    width: 24px;
    height: 24px;
    margin-left: 70px;
    margin-top: 20px;
}

.header {
    height: 60px;
    background: #FFF;
    box-shadow: 0px 0px 2px 0px rgba(29, 33, 38, 0.10), 0px 0px 40px 0px rgba(29, 33, 38, 0.13);
    display: flex;
    justify-content: space-between;
    text-align: center;
}

.header h1 {
    color: #000;
    font-family: Montserrat, sans-serif;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-right: 70px;
    margin-top: 20px;
}